import React from 'react';
import './SearchBar.css';

const sortByOptions = {
'Best Match': 'best_match',
'Highest Rated': 'rating',
'Most Reviewed': 'review_count'
};

class SearchBar extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      term: '',
      location: '',
      sortBy: 'best_match',
    };

    this.handleTermChange = this.handleTermChange.bind(this);

    this.handleLocationChange = this.handleLocationChange.bind(this);

    this.handleSearch = this.handleSearch.bind(this);

  }

  handleTermChange(event) {
    this.setState({
      term: event.target.value
    });
    const submitButton = document.querySelector(".SearchBar-submit a");
    submitButton.innerHTML = "Lets Eat!";
    submitButton.setAttribute("class", "SearchBar-submit-button");
  }
  handleLocationChange(event) {
    this.setState({
      location: event.target.value
    });
    const submitButton = document.querySelector(".SearchBar-submit a");
    submitButton.innerHTML = "Lets Eat!";
    submitButton.setAttribute("class", "SearchBar-submit-button");
  }

  handleSearch(event) {
    if (!this.state.term || !this.state.location) {
      const submitButton = document.querySelector(".SearchBar-submit a");
      submitButton.innerHTML = "Enter a value in both fields";
      submitButton.setAttribute("class", "submit-error");
    } else {
      this.props.searchYelp(this.state.term, this.state.location, this.state.sortBy);
      event.preventDefault();
      document.querySelector('.SearchBar').style.height = "25rem";
    }
  }

  getSortByClass(sortByOption) {
    if (sortByOption === this.state.sortBy) {
      return 'active'
    } else {
      return ''
    }
  };

  handleSortByChange(sortByOption) {
    this.setState({
      sortBy: sortByOption
    });
  }

  renderSortByOptions() {
    return Object.keys(sortByOptions).map(sortByOption => {
      let sortByOptionValue = sortByOptions[sortByOption];
      return <li onClick={this.handleSortByChange.bind(this, sortByOptionValue)} className={this.getSortByClass(sortByOptionValue)} key={sortByOptionValue}>{sortByOption}</li>
    });
  }

  render() {
    return (
  <div className="SearchBar">
  <div className="PageHeader">
      <div className="SiteLogo">
      </div>
  </div>
  <div className="SearchBar-sort-options">
    <ul>
    {this.renderSortByOptions()}
    </ul>
  </div>
  <div className="SearchBar-fields">
    <input onChange={this.handleTermChange} placeholder="Search Business Name or Cuisine" required />
    <input onChange={this.handleLocationChange} placeholder="Enter a City" required />
  </div>
  <div onClick={this.handleSearch} className="SearchBar-submit">
    <a className="SearchBar-submit-button" href="#">Let's Eat!</a>
  </div>
</div>
    )
  }
}

export default SearchBar;
