import React from 'react';
import './Business.css';


class Business extends React.Component {
  render() {
    return (
      <div className="Business">
      <div className="image-container">
        <img src={this.props.business.imageSrc} alt=''/>
        <p className="food-category">{this.props.business.category}</p>
      </div>
      <h2 className="business-name">{this.props.business.name}</h2>
      <div className="Business-reviews">
        <p className="rating">{this.props.business.rating} stars</p>
        <span className="bullet-seperator">&#8226;</span>
        <p className="review">{this.props.business.reviewCount} reviews</p>
      </div>
      <div className="Business-information">
        <div className="Business-address">
          <p>{this.props.business.address}</p>
          <p>{this.props.business.city}</p>
          <p>{this.props.business.state} {this.props.business.zipCode}</p>
        </div>
      </div>
    </div>
    );
  }
}

export default Business;
